: $--color-text-secondary;\n              }\n            }\n\n            &.is-checked {\n              .el-checkbox-button__inner {\n                span {\n                  font-weight: 700;\n                  color: $--color-primary;\n                }\n\n                i:before {\n                  font-weight: 700;\n                  color: $--color-primary;\n                }\n              }\n            }\n          }\n        }\n\n        .dialog-body__recipients-error {\n          margin-top: 8px;\n          margin-left: 16px;\n          font-size: 12px;\n\n          padding: 8px 16px;\n\n          border-radius: 0px 10px 10px 10px;\n\n          background-color: $--color-danger-light-3;\n          font-weight: 600;\n          color: $--color-white\n        }\n      }\n    }\n\n    .jus-ckeditor__parent {\n      .ck-editor {\n        height: 50vh;\n        overflow: hidden;\n\n        .ck-editor__main {\n          height: 90%;\n\n          .ck-editor__editable {\n            height: 100%;\n          }\n        }\n      }\n    }\n\n  }\n}\n\n.management-actions {\n  overflow-x: auto;\n  position: absolute;\n  left: 0;\n  right: 0;\n  background-color: #fff;\n  z-index: 3;\n  padding: 0 20px;\n  transition: all 0.5s ease;\n  display: flex;\n  justify-content:space-between;\n  align-items: center;\n  margin: -44px 0px 0;\n  transform: translateY(-100%);\n  border-radius: 5px 5px 0 0;\n  div {\n    display: flex;\n  }\n  &.active {\n    box-shadow: 0 4px 24px 0 rgba(37, 38, 94, 0.12);\n    margin: 0px;\n    transform: translateY(-29%);\n  }\n  i {\n    cursor: pointer;\n    text-align: right;\n  }\n  button {\n    margin-left: 0 !important;\n    height: 68px;\n    padding: 8px;\n    font-size: 14px;\n    border: 0;\n    border-radius: 0;\n    text-transform: uppercase;\n    &:hover {\n      background-color: #fafafa !important;\n    }\n  }\n  &__length {\n    color: $--color-primary;\n    font-weight: 500;\n    font-size: 14px;\n    width: 42px;\n    justify-content: center;\n    align-items: center;\n    margin-right: 8px;\n    i {\n      margin-right: 4px;\n      font-weight: 600;\n    }\n  }\n  .el-icon-close {\n    padding: 0 20px 0px 8px;\n    font-size: 16px;\n  }\n  &__dialog {\n    .el-message-box__content {\n        padding: 10px 0;\n    }\n    .el-select, .el-date-editor.el-input, .el-transfer {\n      width: 100%;\n    }\n  }\n}\n\n.management-action__dropdown-menu {\n  max-height: 75vh;\n  overflow-y: scroll;\n  overflow-x: hidden;\n  padding-left: 18px;\n\n  .el-dropdown-menu__item {\n    text-align: center;\n    text-transform: uppercase;\n  }\n}\n\n.dialog-actions__change-tags {\n  .el-dialog__body {\n    .change-tags {\n      display: flex;\n\n      .el-select {\n        width: 100%;\n      }\n    }\n  }\n}\n","/* Colors\n-------------------------- */\n\n$--color-white: #fff !default;\n$--color-black: #424242 !default;\n$--color-gray: #979797 !default;\n$--color-gray-light-1: mix($--color-white, $--color-gray, 10%) !default;\n$--color-light-gray: #f3f4f5 !default;\n$--color-whatsapp: #075E54 !default;\n\n$--pj-color-darker-blue: #0A0022 !default;\n$--pj-color-dark-blue: #280BA0 !default;\n$--pj-color-blue: #0756e4 !default;\n$--pj-color-light-blue: #018EF0 !default;\n$--pj-color-lighter-blue: #30A9FB !default;\n$--pj-color-white-blue: #F0F9FE !default;\n$--pj-color-disabled-blue: #9ad2f9 !default;\n\n$-pg-color-overlay: #19415F !default;\n\n$--pj-color-red: #CC4148 !default;\n$--pj-color-light-red: #EC767C !default;\n$--pj-color-disable-red: #FFF2F3 !default;\n\n$--color-primary: $--pj-color-blue !default;\n$--color-primary-light-1: mix($--color-white, $--color-primary, 10%) !default;\n$--color-primary-light-2: mix($--color-white, $--color-primary, 20%) !default;\n$--color-primary-light-3: mix($--color-white, $--color-primary, 30%) !default;\n$--color-primary-light-4: mix($--color-white, $--color-primary, 40%) !default;\n$--color-primary-light-5: mix($--color-white, $--color-primary, 50%) !default;\n$--